@use 'functions' as f;

@mixin roboto{
  font-family: Roboto, sans-serif;
  font-weight: 400;
  font-size: 100%;
  font-display: swap;
}

h1{
  font-weight: 300;
  font-size: f.pxToEm(96);
}

h2{
  font-weight: 300;
  font-size: f.pxToEm(60);
}

h3{
  font-weight: 400;
  font-size: f.pxToEm(48);
}

h4{
  font-weight: 400;
  font-size: f.pxToEm(34);
}

h5{
  font-weight: 400;
  font-size: f.pxToEm(24);
}

h6{
  font-weight: 500;
  font-size: f.pxToEm(20);
}